import React, { Component } from 'react';
// import _ from 'lodash';
import $ from 'jquery';
// components
import ModalTemplate from '../components/modal-template';
// images
import Title from '../assets/images/app-title.png';
import Credits from '../assets/images/sponsors-logo.png';
// import { ListItemIcon } from '@material-ui/core';

class InformationModal extends Component {
  constructor(props){
    super(props);

    this.serviceTerms=[
      {title: '',body:''},
      {title: '1)',body:''},
      {title: '2',body:''},
      {title: '3',body:'\n\n(1)\n\n(2)\n\n(3)\n\n(4)\n\n(5)\n\n(6)\n\n(7)\n\n(8)'},
      {title: '4',body:'\n\n(1)\n\n(2)\n\n(3)\n\n(4)\n\n'},
      {title: '5',body:''},
      {title: '6',body:''},
      {title: '7',body:''},
      {title: '8',body:''},
      {title: '9',body:'\n\nSSL3\n\nLocal Strage\n\n\n\n\n\n'},
      {title: '10',body:''},
      {title: '11',body:''},
    ]

    

    this.informationItems =[
      {title: '',body:'SEKAI NO OWARI EyeLip \nWebSEKAI NO OWARI Sound RPG ',remark:''},
      {title: '',body:'2019.2.4(Mon) ~ 2019.2.11(Mon)',remark:''},
      {title: '',body:'iOS10  SafariAndroid  Chrome58 ',remark:''},
      {title: '',body:'\n\n',remark:''},
      {title: '',body:this.serviceTerms, remark:''},
    ]

  }

  shouldComponentUpdate(nextProps, prevState) {
    if(nextProps.showModal !== this.props.showModal){
      return true
    }
    return false
  }

  handleModal = (showModal) => {
    //this.setState({ showModal })
    this.props.callBackState( showModal );
  }

  createInfomationTable(itemsArray){
    let tableItemsJSX = [];
    for(let index in itemsArray){
      let content;

      if(itemsArray[index].title === ''){
        content= (
          <tbody key={index}>
          <tr>
            <td colSpan='2'><div className='modal-content-jp'>{(typeof(itemsArray[index].body) === 'object')? this.renderInfomationTable(itemsArray[index].body) : this.formatContent(itemsArray[index].body)} </div></td>
          </tr>
          <tr>
            <td>&nbsp;</td>
            <td></td>
          </tr>
          </tbody>
        )
      }else{
        content= (
          <tbody key={index}>
          <tr>
            <td><div className="circle-white">&nbsp;</div> </td>
            <td><div className='modal-content-listTitle'>{itemsArray[index].title}</div> </td>
          </tr>
          <tr>
            <td></td>
            <td> <div className='modal-content-jp'>{(typeof(itemsArray[index].body) === 'object')? this.renderInfomationTable(itemsArray[index].body) : this.formatContent(itemsArray[index].body)} </div></td>
          </tr>
          <tr>
            <td></td>
            <td> <div className='modal-content-jp'>{itemsArray[index].remark} </div></td>
          </tr>
          <tr>
            <td>&nbsp;</td>
            <td></td>
          </tr>
          </tbody>
        )
      }

      tableItemsJSX.push(content);
    }
    return tableItemsJSX
  }

  renderInfomationTable(array){
    return(
      <table>
        {this.createInfomationTable(array)}
      </table>
    )
  }

  formatContent = (content) => {
    return content.split("\n").map((item, key) => {
      return (
        <span key={key}>
          {item}
          <br />
        </span>
      )
    }
    )
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    let style = {
      height: `${window.innerHeight - 190}px`,
      overflow: 'scroll'
    }

    return (
      <div>
        {/* {console.log('info')}         */}
        <ModalTemplate showModal={this.props.showModal}
          callBackState={this.handleModal} 
          shouldCloseOnOverlayClick={true}
          isShort={false}
          isSmall={this.props.isSmall}
          revealModal
          >
           <div className="modal-miniTitle" onClick={this.scrollTop}>
            <br />
            Information
          </div>
          <div className={`modal-content scroll-modal`} style={style}>
            <img src={Title} alt="Title" width='100%' style={{ pointerEvents: 'none' }}/>
            {/* <table>
            {this.renderListFirstLevel()}
            </table> */}
            {this.renderInfomationTable(this.informationItems)}
            <img src={Credits} alt="Credits" width='100%' style={{ pointerEvents: 'none' }}/>
          </div>
        </ModalTemplate>
      </div>
    )
  }
}
export default InformationModal

