import React, { Component } from 'react';
import { connect } from  'react-redux';
import { withRouter } from 'react-router';
import $ from 'jquery';
import _ from 'lodash';
// components
import ModalTemplate from './modal-template';
// images
import HelpIcon from '../assets/images/helpIcon.svg'
import HelpFigure from '../assets/images/helpFigure.png'
import IconNone from '../assets/images/number-none.svg'
import IconOne from '../assets/images/number-one.svg'
import IconTwo from '../assets/images/number-two.svg'
import IconThree from '../assets/images/number-three.svg'
import IconFour from '../assets/images/number-four.svg'
import IconFive from '../assets/images/number-five.svg'
import IconSix from '../assets/images/number-six.svg'
import IconSeven from '../assets/images/number-seven.svg'
import IconEight from '../assets/images/number-eight.svg'
import IconNine from '../assets/images/number-nine.svg'
import IconTen from '../assets/images/number-ten.svg'
// styles
import '../assets/styles/hamburger-menu.css'

//actions
import { setSoundControllerOpenState } from '../actions/sounds';

class HelpModal extends Component {
  state = {
    showModal: false
  }

  shouldComponentUpdate(nextProps, prevState) {
    if(prevState.showModal !== this.state.showModal){
      return true
    }
    return false
  }

  items = {
    '01': {num: 1,icon: IconOne,title: '',bodyJP:'',bodyEN:'After visiting all 5 spots, you can unlock a special pre-release song and get an original gift.'},
    '02': {num: 2,icon: IconTwo,title: '',bodyJP:'',bodyEN:'Open help page to check how to use this app.'},
    '03': {num: 3,icon: IconThree,title: '',bodyJP:'',bodyEN:'Open detailed information about this campaign.'},
    '04': {num: 4,icon: IconFour,title: '',bodyJP:'',bodyEN:'Swipe to look around your Map. Pinch to change the zoom level.'},
    '05': {num: 5,icon: IconFive,title: '',bodyJP:'',bodyEN:'Your current location and compass direction.'},
    '06': {num: 6,icon: IconSix,title: '',bodyJP:'',bodyEN:'Open map covering all spots.'},
    '07': {num: 7,icon: IconSeven,title: '',bodyJP:'',bodyEN:'Center your map to your current location.'},
    '08': {num: 8,icon: IconEight,title: '',bodyJP:'',bodyEN:'Show song title currently selected.'},
    '09': {num: 9,icon: IconNine,title: '',bodyJP:'',bodyEN:'Open list of songs.'},
    '10': {num: 10,icon: IconTen,title: '',bodyJP:'',bodyEN:'Music player control panel.'},
  }

  handleOpenModal = () => {
    this.props.setSoundControllerOpenState(false);
    this.setState({ showModal: true });
  }

  handleModal = (showModal) => {
    this.setState({ showModal })
  }

  renderList () {
    let arr = _.map(this.items, (item, key)=>{
      return(
        <tbody key={key}>
        <tr>
          <td><div><img src={item.icon} alt="icon" width="17" height="17" align="top"/></div></td>
          <td> <div className='modal-content-listTitle'>{item.title} </div></td>
        </tr>
        <tr>
          <td></td>
          <td> <div className='modal-content-jp'>{item.bodyJP} </div></td>
        </tr>
        <tr>
          <td></td>
          <td> <div className='modal-content-en'>{item.bodyEN} </div></td>
        </tr>
        <tr>
          <td>&nbsp;</td>
          <td></td>
        </tr>
        </tbody>
      )
    });
    return _.sortBy(arr, ['key'])
  }

  scrollTop = event => {
    event.preventDefault();
    $(".scroll-modal").animate({ scrollTop: 0 }, "slow");
    return false;
  }

  render () {
    let style = {
      height: `${window.innerHeight - 190}px`,
      overflow: 'scroll'
    }
    // console.log('help')
    return (
      <div>
        <div className="info">
          <img src={HelpIcon} alt="" width="40" height="40" onClick={this.handleOpenModal} />
        </div>
        {/*<InformationIcon onClick={this.handleOpenModal} style={this.props.style}/>*/}
        <ModalTemplate showModal={this.state.showModal}
          callBackState={this.handleModal}
          isShort={false}
          isSmall={this.props.isSmall}
          revealModal
          >
          {/*<div className={`${this.props.isSmall? 'modal-miniTitle-small' : 'modal-miniTitle'}`}>*/}
          <div className="modal-miniTitle" onClick={this.scrollTop}>
            <br />
            Help
          </div>
          {/*<div className={`${this.props.isSmall ? 'modal-content-small' : 'modal-content'} scroll-modal`} align='center' style={style}>*/}
          <div className={`modal-content scroll-modal`} align='center' style={style}>
            <div>
              <img src={HelpFigure} alt="Help" width='100%' style={{ pointerEvents: 'none' }}/>
            </div>
            <table>
              {this.renderList()}
            </table>
            <table width='100%'>
              <tbody>
              <tr><td><img src={IconNone} alt="icon" width="17" height="17" align="top"/></td><td><div className='modal-content-listTitle'></div></td></tr>
              <tr><td></td><td><div className='modal-content-jp'></div></td></tr>
              <tr><td></td><td><div className='modal-content-en'>When you encounter any issues, try reloading the webpage.</div></td></tr>
              <tr>
                <td>&nbsp;</td>
                <td></td>
              </tr>
              </tbody>
            </table>
          </div>
        </ModalTemplate>
      </div>
    )
  }
}

export default connect(state => state, {setSoundControllerOpenState})(withRouter(HelpModal))

