import React, { Component } from 'react'
import ModalTemplate from './modal-template'
import Title from '../assets/images/app-title-small.png'

class PrizeInfoModal extends Component {
  titleStyle = {
    marginTop: `${(window.innerHeight - 118) * .25-80}px`,
  }

  contentStyle = {
    marginTop: `${(window.innerHeight - 118) * .5}px`,
  }

  onModalClosed = () => {
    if (this.props.onModalClosed) {
      this.props.onModalClosed()
    }
  }

  render () {
    return (
      <ModalTemplate defaultShowModal={true}
                    glow
                    callbackClose={this.onModalClosed} >
        <div className='modal-title'>
          
        </div>
        <div align='center' style={this.titleStyle}>
          <img src={Title} alt="Title" width='100%' style={{pointerEvents: 'none' }}/>
          <div className='modal-miniTitle' align='center'>
          
          </div>
        </div>

        <div className='soundInfoContent' align='center' style={this.contentStyle}>
          <br></br>
          
        </div>       
      </ModalTemplate>
    )
  }
}
export default PrizeInfoModal
